import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as dealDetailsActions from "../../actions/dealDetailsActions";
import ViolationsPage from "./ViolationsPage";
import getParameterByName from "../common/getParameterByName";

class ViolationsContainer extends React.Component {
  componentDidMount() {
    this.props.match.params.dealType === "rp"
      ? this.props.actions.loadRPViolations(getParameterByName("property_id"))
      : this.props.actions.loadViolations(
          this.props.match.params.dealId
            ? this.props.match.params.dealId
            : getParameterByName("deal_id")
            ? getParameterByName("deal_id")
            : ""
        );
  }

  render() {
    return (
      <ViolationsPage
        violations={this.props.dealDetails.violations}
        loading={this.props.dealDetails.loading}
        noViolations={this.props.dealDetails.noViolations}
      />
    );
  }
}

function mapStateToProps(state) {
  return {
    dealDetails: state.dealDetails,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(dealDetailsActions, dispatch),
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ViolationsContainer);
