import React from "react";
import Modal from "react-modal";
import Violation from "./Violation";

const ViolationsPage = (props) => {
  let noViolations = null;
  const violations = props.violations.map((violation, index) => (
    <Violation
      key={index}
      propertyName={
        violation.property_address ? violation.property_address : ""
      }
      propertyCityState={violation.city_state ? violation.city_state : ""}
      DOBComplaints={violation.dob_complaints}
      HPDComplaints={violation.hpd_complaints}
      ECBComplaints={violation.ecb_complaints}
      DOBViolations={violation.dob_violations}
      HPDViolations={violation.hpd_violations}
      ECBViolations={violation.ecb_violations}
      totalComplaints={violation.total_complaints}
      totalViolations={violation.total_violations}
    />
  ));

  if (props.noViolations) {
    noViolations = (
      <div>
        <div className="grey-circle">
          <i className="material-icons">business</i>
        </div>
        <h2 className="no-results">
          Violation data is currently only available on New York City properties
        </h2>
      </div>
    );
  }

  return (
    <div className="violations-page">
      <Modal
        className="loading-modal"
        isOpen={props.loading}
        ariaHideApp={false}
        contentLabel="Modal"
        shouldCloseOnOverlayClick={false}
      >
        <div className={props.loading ? "centered-spinner" : "hidden"}>
          <img
            src="/images/icons/spin.svg"
            className="loading-spinner"
            alt="loading spinner"
          />
        </div>
      </Modal>
      {noViolations}
      {violations}
      <div className={props.violations.length < 1 ? "hidden" : ""}>
        <p className="violation-footer">
          For violation removal and remediation call Jack Jaffa and Associates
          at 718-855-6110 or visit{" "}
          <a
            href="http://www.jackjaffa.com"
            target="_blank"
            rel="noopener noreferrer"
          >
            jackjaffa.com
          </a>
        </p>
      </div>
    </div>
  );
};

export default ViolationsPage;
